body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.list-wrapper ul {
  border: 1px solid #ccc;
  padding: 0;
  margin: 0 10px; }

li.clickable:hover {
  cursor: pointer; }

li.list-element {
  position: relative;
  background-color: #ccc;
  border-bottom: 1px solid #ccc;
  padding: 10px;
  margin: 5px;
  list-style: none;
  text-align: left;
  height: 20px; }
  li.list-element:hover {
    background-color: #e7e7e7; }
  li.list-element span {
    margin-top: 2px; }
    li.list-element span.buttons-wrapper {
      position: absolute;
      right: 10px; }
      li.list-element span.buttons-wrapper span {
        padding-left: 15px; }
        li.list-element span.buttons-wrapper span:hover {
          cursor: pointer; }
        li.list-element span.buttons-wrapper span svg:hover.fa-edit {
          color: lightgreen; }
        li.list-element span.buttons-wrapper span svg:hover.fa-trash-alt {
          color: red; }
  li.list-element .title {
    position: absolute;
    left: 10px; }
  li.list-element .sub-title {
    position: absolute;
    left: 100px;
    font-size: 13px;
    font-style: italic; }

.customers-list-wrapper {
  padding: 0 50px; }

h1 {
  color: white; }

.customers {
  min-height: 100vh; }

ul.menuHeader li {
  display: inline;
  list-style-type: none;
  margin: 0; }

ul.menuHeader {
  background-color: #282c34;
  padding: 0;
  margin-top: 0; }

ul.menuHeader li a {
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block; }

